
.btn_choose {
  width: 25px;
  height: 25px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 15px;
  border-radius: 20%;
  background-size: 25px 25px;
}
.select {
  background-image: url(../../img/select.png);
}
.unselect {
  background-image: url(../../img/unselect.png);
}
.list_container {
  position: relative;
}
.segment {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}



